import React from 'react';
import styled from 'styled-components';
import { CarmapListApi } from '../../lib/apis/CarmapListApi';
import { useState } from 'react';
import { useEffect } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import img from '../../assets/img/404.png';
import { useRef } from 'react';
import { RepairShop } from '../CarDamage/RepairShop';

const { kakao } = window;

const S = {
  Box: styled.div`
    width: 350px;
    height: 350px;
    border: 2px solid #2e2e2e;
    margin: 0 auto;
  `,
  Table: styled.table`
    width: 385px;
    margin: 0 auto;
    border-bottom: 1px solid #5c5c5c;
    border-top: 2px solid #2e2e2e;
    font-size: 12px;
    margin-bottom: 10px;
  `,
  Head: styled.thead`
    /* border: 1px solid blue; */
    text-align: center;
    background-color: #05194d;
    color: #fff;
  `,
  Tr: styled.tr`
    border: 1px solid blue;
    height: 30px;
    cursor: pointer;
    &.list {
      font-size: 10px;
    }
  `,
  Th: styled.th`
    width: 70px;
    background-color: white;
    color: #143c56;
    font-weight: 700;
    vertical-align: middle !important;
  `,
  Td: styled.td`
    border: 1px solid #e4dcd3;
    vertical-align: middle !important;
    &.name {
      width: 80px;
      text-align: center;
      white-space: pre-line;
      line-height: 15px;
    }
    &.Read {
      width: 170px;
    }
    &.number {
      text-align: center;
      width: 70px;
    }
  `,
  body: styled.tbody``,
  TableBox: styled.div`
    position: relative;
    margin-top: 20px;
    width: auto;
    height: 360px;
  `,
  Pagination: styled.div`
    display: flex;
    justify-content: center;
    margin-bottom: 20px;
  `,
  PageButton: styled.button`
    margin: 0 5px;
    cursor: pointer;
    &.active {
      color: red;
    }
  `,
  error: styled.img`
    width: 300px;
    height: 300px;
    position: absolute;
    margin: 0 auto;
    right: 0;
    left: 0;
  `,
};

const MapBox = () => {
  const navigate = useNavigate();
  const location = useLocation();
  const queryParams = new URLSearchParams(location.search);
  const page = parseInt(queryParams.get('page')) || 1;

  const [data, setData] = useState([]);
  const [currentPage, setCurrentPage] = useState(page);
  const pageSize = 10;
  const totalItems = data.length;

  const [latitude, setLatitude] = useState(null);
  const [longitude, setLongitude] = useState(null);
  const [error, setError] = useState(null);
  const [mapLoaded, setMapLoaded] = useState(false); // Map   

  //    state 
  const [map, setMap] = useState(null);
  //    state 
  const [markers, setMarkers] = useState([]);

  const handlePageChange = (newPage) => {
    navigate(`/map?page=${newPage}`);
    setCurrentPage(newPage);
  };

  useEffect(() => {
    getList();
  }, [currentPage]);

  const getList = async () => {
    try {
      const response = await CarmapListApi();
      setData(response);
    } catch (error) {
      console.error('  :', error);
    }
  };
  const indexOfLastItem = currentPage * pageSize;
  const indexOfFirstItem = indexOfLastItem - pageSize;
  const currentItems = data.slice(indexOfFirstItem, indexOfLastItem);

  const paginate = (pageNumber) => {
    setCurrentPage(pageNumber);
  };
  const mapRef = useRef(null);

  //  geolocation      
  useEffect(() => {
    if ('geolocation' in navigator && !mapLoaded) {
      navigator.geolocation.getCurrentPosition(
        function (position) {
          setLatitude(position.coords.latitude);
          setLongitude(position.coords.longitude);
          setMapLoaded(true); // Map   
        },
        function (error) {
          setError(error.message);
          setMapLoaded(true); // Map   
        },
      );
    } else {
      setError('Geolocation is not available in your browser.');
      setMapLoaded(true); // Map   
    }
  }, [mapLoaded]);

  useEffect(() => {
    const container = mapRef.current;

    if (mapLoaded) {
      const options = {
        center: new window.kakao.maps.LatLng(latitude, longitude),
        level: 3,
      };
      const newMap = new kakao.maps.Map(container, options);
      setMap(newMap);

      //     
      markers.forEach((marker) => marker.setMap(null));

      const newMarkers = RepairShop.map((el) => {
        const markerImage = new window.kakao.maps.MarkerImage(
          el.img, //  URL
          new window.kakao.maps.Size(50, 50), //  
          { offset: new window.kakao.maps.Point(15, 30) }, //  
        );
        return new window.kakao.maps.Marker({
          map: newMap, //     
          position: new window.kakao.maps.LatLng(el.lat, el.lng),
          title: el.title,
          image: markerImage,
        });
      });

      //    
      setMarkers(newMarkers);
    }
  }, [latitude, longitude, mapLoaded]);

  const ChangeMap = (CenterName) => {
    //   CenterName  
    const title = CenterName; // CenterName     row title  

    const selectedShop = RepairShop.find((shop) => shop.title === title);
    console.log(selectedShop.lat, selectedShop.lng);
    if (selectedShop && selectedShop.lat && selectedShop.lng) {
      //   , lat  lng      
      map.panTo(
        new window.kakao.maps.LatLng(selectedShop.lat, selectedShop.lng),
      );
    }
  };

  return (
    <>
      {console.log(latitude, longitude)}
      <S.Box id="map" ref={mapRef}></S.Box>
      <S.TableBox>
        <S.Table>
          <S.Head>
            <S.Tr>
              <S.Td></S.Td>
              <S.Td></S.Td>
              <S.Td></S.Td>
            </S.Tr>
          </S.Head>
          <S.body>
            {currentItems.length === 0 ? (
              <S.error src={img} />
            ) : (
              currentItems.map((item, index) => {
                const name = item.name; // item.name const  name 
                const repairName = name.replace(/ /, '\n'); //   \n 

                return (
                  <S.Tr
                    className="list"
                    key={index}
                    onClick={() => ChangeMap(item.name)}
                  >
                    <S.Td className="name">{repairName}</S.Td>
                    <S.Td className="Read">{item.address}</S.Td>
                    <S.Td className="number">{item.tell}</S.Td>
                  </S.Tr>
                );
              })
            )}
          </S.body>
        </S.Table>
      </S.TableBox>
      <S.Pagination>
        {Array.from(
          { length: Math.ceil(totalItems / pageSize) },
          (_, index) => (
            <S.PageButton
              className={currentPage === index + 1 ? 'active' : ''}
              key={index}
              // onClick={() => paginate(index + 1)}
              onClick={() => handlePageChange(index + 1)}
            >
              {index + 1}
            </S.PageButton>
          ),
        )}
      </S.Pagination>
    </>
  );
};

export default MapBox;
